// }

//WATCHING VIDEO COMPONENT
AFRAME.registerComponent('watching-video', {
  init: function () {

    this.el.addEventListener('mouseenter', function (event) {
      WATCHING_VIDEO = true;
      console.log("WATCHING_VIDEO = true");
    });

    this.el.addEventListener('mouseleave', function (event) {
      WATCHING_VIDEO = false;
      console.log("WATCHING_VIDEO = false");
    });
  }
});



